  width: 100%;
  height: 100%;
  object-fit: contain;
  z-index: 3;
  background: #000;
  position: absolute;
}

#map {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
}

.map-container--hide-markers .marker {
  display: none;
}

.marker {
  width: 40px;
